import request from '@/static/request.js'
export default {
  data() {
    return {
      name: '',
      gender: '',
      telephone: '',
      correctAuthcode: '',
      authcode: '',
      email: '',
      wechat: '',
      showMoreInfo: false,
      showCheckBtn: true,
      showAlert: false,
    }
  },
  methods: {
    sendAuthcode() {
      const vm = this;
      request
        .get('weixinRegister/', {
          user_telephone: vm.telephone,
        })
        .then(res => {
          if(res.data.auth_code === '000000') {
            uni.showToast({
              title: '号码已被注册！',
              duration: 1000,
            })
          }
          else if(res.data.statuscode === '400') {
            uni.showToast({
              title: '发送失败！',
              duration: 1000,
            })
          }
          else{
            vm.correctAuthcode = res.data.auth_code;
            uni.showToast({
              title: '发送成功',
              duration: 1000,
            })
          }
        })
    },
    checkAuthcode() {
      const vm = this;
      if(vm.authcode === vm.correctAuthcode) {
        vm.showMoreInfo = true;
        vm.showCheckBtn = false;
        request
          .get('weixinAddUser/', {
            user_telephone: vm.telephone,
          })
          .then(res => {
          })
      }
      else {
        uni.showToast({
          title: '验证码错误！',
          duration: 1000,
        })
      }
    },
    genderChange(e) {
      this.gender = e.currentTarget.dataset.gender
    },
    showRegister() {
      const vm = this;
      request
        .get('initializeData/', {
          user_telephone: vm.telephone,
          user_name: vm.name,
          user_weixinnumber: vm.wechat,
          user_gender: vm.gender,
          user_email: vm.email,
        })
        .then(res => {
          if(res.data.statuscode === '400') {
            uni.showToast({
              title: '注册失败！',
              duration: 1000,
            })
          }
          else{
            uni.showToast({
              title: '注册成功！',
              duration: 1000,
            });
            uni.navigateTo({
              url: '../user/user'
            });
          }
        })
    },
    checkEmail() {
      const mail = this.email
      const reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if(reg.test(mail) === true) {
        this.showAlert = false
      }
      else this.showAlert = true
    }
  },
};
